@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap);
.Socials {
  display: flex;
  font-size: 2.5rem;
}

.Socials a {
  height: 30%;
  color: #fff;
  margin: 0 0.5rem;
  text-shadow: 0 0 1rem #000;
  display: block;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.Socials a:hover {
  text-shadow: 0 0.7rem 0.5rem #000;
  transform: translateY(-5px);
  -webkit-transform: translateY(-5px);
  -moz-transform: translateY(-5px);
  -ms-transform: translateY(-5px);
  -o-transform: translateY(-5px);
}

@font-face {
  font-family: "aristotelica";
  src: url(/static/media/aristotelica.display-trial-regular.42c60a5f.ttf);
}

.saad {
  font-family: aristotelica;
  font-size: 4rem;
  letter-spacing: 0.5rem;
}

.services {
  padding-left: 0;
  list-style: none;
}

.services a {
  text-decoration: none;
  color: #fff;
  line-height: 2rem;
  position: relative;
  padding-bottom: 0.3rem;
}

.services a::after {
  position: absolute;
  content: "";
  background: white;
  width: 0;
  height: 1px;
  bottom: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  -ms-transition: 0.3s ease-in-out;
  -o-transition: 0.3s ease-in-out;
}

.services a:hover::after {
  width: 100%;
}

.end_text {
  background: #111;
}
.end_text p {
  margin: 0;
  padding: 0;
  line-height: 5rem;
}

@media (max-width: 768px) {
  .Socials {
    font-size: 1.7rem;
  }
  .saad {
    font-size: 2.5rem;
  }
}

.header {
  text-align: center;
}

.header h2,
.header h1 {
  text-shadow: 0 0 0.2rem red, 0 0 0.4rem red, 0 0 0.6rem red, 0 0 0.8rem red,
    0 0 1rem red, 0 0 1.2rem red;
}

.header h2 {
  font-size: 2.5rem;
}

.header h1 {
  font-size: 4rem;
}

/* mrsaad animation */
#logo {
  -webkit-animation: fill 1s ease forwards 4s, zoom 4s ease forwards;
          animation: fill 1s ease forwards 4s, zoom 4s ease forwards;
  overflow: visible;
}
@-webkit-keyframes fill {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}
@keyframes fill {
  from {
    fill: transparent;
  }
  to {
    fill: white;
  }
}

@-webkit-keyframes zoom {
  from {
    transform: scale(1.8);
  }
  to {
    transform: scale(1);
  }
}

@keyframes zoom {
  from {
    transform: scale(1.8);
  }
  to {
    transform: scale(1);
  }
}

#logo path:nth-child(1) {
  stroke-dasharray: 767;
  stroke-dashoffset: 767;
  -webkit-animation: line 3s ease-in-out forwards;
          animation: line 3s ease-in-out forwards;
}
#logo path:nth-child(2) {
  stroke-dasharray: 254;
  stroke-dashoffset: 254;
  -webkit-animation: line 3s ease-in-out forwards 0.3s;
          animation: line 3s ease-in-out forwards 0.3s;
}
#logo path:nth-child(3) {
  stroke-dasharray: 594;
  stroke-dashoffset: 594;
  -webkit-animation: line 3s ease-in-out forwards 0.6s;
          animation: line 3s ease-in-out forwards 0.6s;
}
#logo path:nth-child(4) {
  stroke-dasharray: 516;
  stroke-dashoffset: 516;
  -webkit-animation: line 3s ease-in-out forwards 0.9s;
          animation: line 3s ease-in-out forwards 0.9s;
}
#logo path:nth-child(5) {
  stroke-dasharray: 516;
  stroke-dashoffset: 516;
  -webkit-animation: line 3s ease-in-out forwards 1.2s;
          animation: line 3s ease-in-out forwards 1.2s;
}
#logo path:nth-child(6) {
  stroke-dasharray: 586;
  stroke-dashoffset: 586;
  -webkit-animation: line 3s ease-in-out forwards 1.5s;
          animation: line 3s ease-in-out forwards 1.5s;
}

@-webkit-keyframes line {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes line {
  100% {
    stroke-dashoffset: 0;
  }
}

@media (max-width: 768px) {
  .header h2 {
    font-size: 2rem;
  }
  .header h1 {
    font-size: 1.7rem;
  }
  #logo {
    width: 15rem;
  }
  @-webkit-keyframes zoom {
    from {
      transform: scale(1.3);
    }
    to {
      transform: scale(1);
    }
  }
  @keyframes zoom {
    from {
      transform: scale(1.3);
    }
    to {
      transform: scale(1);
    }
  }
}

.About img {
  max-width: 25vw;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 25px -5px,
    rgba(0, 0, 0, 0.04) 0px 10px 10px -5px;
}
.header_text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 800px) {
  .About img {
    max-width: 70vw;
  }
  .About {
    margin-top: 5rem;
  }
}

.Contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 65vh;
  padding: 1rem 2rem 1rem 2rem;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.Contact form {
  width: 70%;
}

textarea {
  resize: none;
}

@media (max-width: 786px) {
  .Contact form {
    width: 100%;
  }
}

.Register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 65vh;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.parent {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .register_area {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}

.Login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65vh;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.Login form {
  width: 50%;
}

@media (max-width: 768px) {
  .Login form {
    width: auto;
    width: initial;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #fff;
}
body::before {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -29;
  background: linear-gradient(to right, #f953c6, #b91d73);
}

.border-b {
  border-bottom: 2px solid #555;
}
.navigation_bar {
  z-index: 100;
  position: fixed;
  transition: all 0.5s ease;
}
.sticky {
  background: #111111;
}
.main_div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.inputs {
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #fff !important;
  color: #fff;
}
.inputs::-webkit-input-placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}
.inputs:-ms-input-placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}
.inputs::-ms-input-placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}
.inputs,
.inputs:focus,
.inputs::placeholder {
  background: transparent;
  box-shadow: none;
  color: #fff;
}

.privacy a,
.terms a {
  color: #fff;
}
.privacy,
.terms {
  margin-top: 3rem;
}
.privacy p,
.terms p {
  margin: 2.5rem 0;
}
@media (max-width: 768px) {
  body::before {
    background: linear-gradient(70deg, #7f7fd5, #86a8e7, #91eae4);
  }
  .scrollbar-track {
    display: none !important;
  }
}

